<template>
  <div id="app">
    <!--// 頭部-->
    <div class="title">{{comDatas.title}}</div>
    <!--轮播图-->
    <div class="show-img">
      <template>
        <el-carousel :interval="5000" arrow="false" class="carousel-box">
          <el-carousel-item v-for="images in imgList" :key="images">
            <img  v-bind:src="images" alt="">
          </el-carousel-item>
        </el-carousel>
      </template>
    </div>
    <!--// 商品價錢-->
    <el-row class="sales_wrap" style="width: 95%;margin: 0 auto;">
      <el-col :span="24">
        <div class="sales-price">
          <span class="price">{{comDatas.currency_front_symbol}}{{comDatas.price}}</span>
          <span class="sales_info">
            <div>原價<del>{{comDatas.currency_front_symbol}}{{comDatas.Original_Price}}</del></div>
          </span>
          <ul v-for="label in labels"  class="label-list">
            <li>{{label}}</li>
          </ul>
        </div>
      </el-col>
      <el-col :span="24">
        <div class="f-hidden">
          <div class="f-left">
              <span class="qg">限時搶購</span>
          </div>
          <div class="sales-time f-right">
            <div class="over-test"><img src="@/assets/time.png" alt="">距离结束:</div>
            <div class="timer">
            </div>
          </div>
        </div>
      </el-col>
      <el-col :span="24">
        <div class="b-line">
          <span>已經搶購:</span>
          <span class="f-right progress-box">
            <el-progress :percentage='alreadyBought' color="red"></el-progress>
          </span>
        </div>
      </el-col>
      <el-col :span="24">
        <div class="order-btn">
          <router-link to="/setMeal">
          <el-button type="warning">立即搶購</el-button>
          </router-link>
        </div>
      </el-col>
      <el-col :span="24">
        <div class="p_relative detail-box">
          <span>商品詳情</span>
          <div class="line"></div>
        </div>
      </el-col>


      <el-col :span="24">
        <div id="commodity-box"></div>
      </el-col>
    </el-row>

    <!--// 商品內容-->
    <div class="f_footer">
      <div class="footer-box">
        <router-link to="/setMeal">
          <span >立即購買</span>
        </router-link>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'APP',
  data: function () {
    return {
      comDatas: [], // 商品的全部詳情
      imgList: [],  // 輪播圖
      labels : [],   // 標籤
      alreadyBought : 0  // 已經購買
    }
  },
  created () {
    this.getComData();
  },
  methods: {
    getComData () {
      var http = 'http://cod.hzhanghuan.com';
      var idArr = window.location.search.split("=");
      var that = this;
      var intDiff;
      that.$axios.get( http+"/api/v1/Cod/goodsdetail?id="+idArr[1])
      // that.$axios.get(http + "/api/v1/Cod/goodsdetail?id=54")
        .then(response => {

          var commodityData = response.data.data;
          console.log(commodityData);
          that.comDatas = commodityData;

          // 图集
          var images = commodityData.images.replace(/&quot;/g, '"');
          var imagesObj = JSON.parse(images);
          for (var i in imagesObj) {
            that.imgList.push(http + imagesObj[i]);
          }
          //商品内容
          var comContent = commodityData.content;
          var detailHtml = comContent;
          $("#commodity-box").append(detailHtml);
          // 商品標籤
          var labelText = commodityData.labels.replace(/&quot;/g, '"');
          var labelObj = JSON.parse(labelText);
          for (var i in labelObj){
            that.labels.push(labelObj[i]);
          }
          // 已經購買
          var inventory = commodityData.Inventory;  // 庫存
          var sales = commodityData.sales ; //銷量
          that.alreadyBought = Math.round((commodityData.sales / commodityData.Inventory)*10);

          console.log(that.alreadyBought);
          // 倒计时
          intDiff = commodityData.rush_time * 3600 - 1;
          return intDiff;
        });
      window.setInterval(function () {
        var day = 0,
          hour = 0,
          minute = 0,
          second = 0; //时间默认值
        if (intDiff > 0) {
          hour = Math.floor(intDiff / (60 * 60)) - (day * 24);
          minute = Math.floor(intDiff / 60) - (day * 24 * 60) - (hour * 60);
          second = Math.floor(intDiff) - (day * 24 * 60 * 60) - (hour * 60 * 60) - (minute * 60);
        }

        var hourDiv = "<span id='h'>" + hour + "時" + "</span>";
        $(".timer").html(hourDiv + minute + "分" + second + "秒");

        intDiff--;
      }, 1000);
    },
  }
}

</script>

<style>
.title {
  font-size: 20px;
  padding: 2% 4%;
  border: 1px solid #e6e6e6;
  font-weight: bold;
  line-height: 28px;
  background-color: #0a0a0a;
  color: #fff;
}
.card-title {
  display: inline-block;
  border-left: 4px solid #f12222;
  padding-left: 15px;
}
.el-card__header {
  padding: 15px;
}
.box-card {
  width: 98%;
  margin: 20px auto;
}
.card-img {
  width: 100%;
  margin: 0 auto;
}
.card-img img {
  width: 100%;
  height: 100%;
}
.show-img {
  width: 100%;
  height: auto;
}
.show-img {
  width: 100%;
  height: auto;
}
.sales-price {
  margin-bottom: 15px;
  border-bottom: 1px dashed #c4c4c4;
  padding-bottom: 15px;
}
/*標籤*/
.label-list {
  display: inline-block;
  overflow: hidden;
}
.label-list li {
  float: left;
  padding: 2px 4px;
  margin-right: 10px;
  background-color: #ff0000;
  color: #fff;
  border-radius: 4px;
  font-size: 14px;
}
.price {
  display: inline-block;
  font-size: 32px;
  padding-top: 30px;
  font-weight: 700;
  line-height: 20px;
  color: #ff0000;
}
.sales_info {
  display: inline-block;
  font-size: 14px;
  color: #666666;
  margin: 5px 0 0 0;
}
.f-hidden {
  overflow: hidden;
  border-bottom: 1px dashed #c4c4c4;
  padding-bottom: 15px;
  margin-bottom: 15px;
}
.f-right {
  float: right;
}
.qg {
  padding: 5px 10px;
  height: 30px;
  line-height: 30px;
  color: #fff;
  background-color: #000;
}
.f-left {
  float: left;
}
.sales-time {
  color: #000;
  font-size: 8px;
  padding-top: 10px;
}
.over-test {
  display: inline-block;
  position: relative;
  font-weight: bold;
  padding-left: 25px;
  font-size: 16px;
}
.progress-box {
  display: inline-block;
  width: 70%;
}
.b-line {
  border-bottom: 1px dashed #c4c4c4;
  margin-bottom: 15px;
  padding-bottom: 15px;
}
.over-test img {
  position: absolute;
  left: 0;
  top: -2px;
}
.timer {
  display: inline-block;
  font-weight: bold;
  margin-left: 5px;
  font-size: 16px;
}
.order-title {
  line-height: 20px;
  margin: 10px auto;
}
.title-info {
  font-size: 12px;
  color: #666666;
  font-weight: bold;
  margin: 10px 0;
}
.p_relative {
  position: relative;
}
.span-icon {
  padding-left: 20px;
  margin-right: 10px;
}
.show-icon {
  position: absolute;
  top: 0;
  left: 0;
}
/*商品詳情*/
.detail-box {
  width: 100%;
  text-align: center;
  margin: 20px auto 0 auto;
}
.detail-box span{
  color: #fff;
  background-color: #000;
  padding: 2px 4px;
  -webkit-border-radius: 4px;
  -moz-border-radius: 4px;
  border-radius: 4px;
  z-index: 2;
  position: relative;
}
.line {
  width: 100%;
  border-bottom: 1px solid #c4c4c4;
  position: absolute;
  left: 0;
  top: 8px;
  z-index: 1;
}
/*底部*/
.f_footer {
  position: fixed;
  height: 45px;
  line-height: 45px;
  bottom: 0;
  left: 0;
  width: 100%;
  color: #f1f1f1;
}

.footer-box {
  overflow: hidden;
  max-width: 640px;
  background-color: #ff0000;
  text-align: center;
  margin: 0 auto;
}
.footer-box a {
  text-decoration: none;
  color: #fff;
}
.left_span {
  float: left;
  margin-left: 10px;
  color: #ba7;
}

.right_span {
  float: right;
  margin-right: 10px;
  cursor: pointer;
  color: #ba7;
}
#commodity-box {
  margin: 20px auto;
  padding-bottom: 30px;
}
.order-btn {
  width: 90%;
  margin: 0 auto;
}
.order-btn .el-button {
  width: 100%;
}
</style>



